import React, { Component } from 'react';
import Plate from './plate.jsx';
import '../../style/food.scss';

class Food extends Component {
  constructor(props) {
    super(props);
    this.myMove = this.myMove.bind(this);
  }
  state = {};

  myMove() {
    console.log('getin');
    var elem = document.getElementById('item1');
    console.log(elem);
    var pos = 0;
    var id = setInterval(frame, 10);
    function frame() {
      if (pos == -100) {
        clearInterval(id);
      } else {
        pos = pos - 1;
        elem.style.left = `${pos}%`;
        console.log(elem.style.left);
      }
    }
  }

  render() {
    return (
      <React.Fragment>
        <div className="plate">
          <Plate />
          <div className="item-container">
            <img
              src={require('../../imgs/food/steak.png')}
              id="item1"
              className="item"
              onClick={this.myMove}
            />
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Food;
